import {timeFormat} from '@/uview/libs/function/timeFormat.js';
module.exports = {
	timeFrom(timestamp = null, format = 'yyyy-mm-dd'){
		if (timestamp == null) timestamp = Number(new Date());
		timestamp = parseInt(timestamp);
		// ,js(13),(10)
		if(timestamp.toString().length == 10) timestamp *= 1000;
	    var timer =  (new Date()).getTime() - timestamp;
		timer = parseInt(timer / 1000);
		// 5,"",
		let tips = '';
		switch(true) {
			case timer < 300: 
				tips = ''; break;
			case timer >= 300 && timer < 3600:
				tips = parseInt(timer / 60) + ''; break;
			case timer >= 300 && timer < 86400:
				tips = parseInt(timer / 3600) + ''; break;
			case timer >= 300 && timer < 2592000:
				tips = parseInt(timer / 86400) + ''; break;
			default :
				tips = timeFormat(timestamp, format);
		}
	    return tips;
	}
}