module.exports = {
	/**
	 * https://www.jianshu.com/p/fdbf293d0a85
	 * uuidGlobally Unique Identifier, uuid(Universally Unique IDentifier) 
	 * ,,v-for,index
	 * item"",
	 * v-for,idindex
	 * @param {Number} len uuid
	 * @param {Boolean} firstU "u"
	 * @param {Nubmer} radix uuid(),2-,8-,10-,16-
	 */
	guid(len = 32, firstU = true, radix = null) {
		let chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
		let uuid = [];
		radix = radix || chars.length;

		if (len) {
			// uuid,,0|x,x,
			for (let i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix];
		} else {
			let r;
			// rfc4122uuid,
			uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
			uuid[14] = '4';

			for (let i = 0; i < 36; i++) {
				if (!uuid[i]) {
					r = 0 | Math.random() * 16;
					uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
				}
			}
		}
		// ,u,,guuididclass
		if(firstU) {
			uuid.shift(); 
			return 'u' + uuid.join('');
		} else {
			return uuid.join('');
		}
	}
}
