// postgeturl
import {
	queryParams
} from '@/uview/libs/function/queryParams.js';
// 
import {
	route
} from '@/uview/libs/function/route.js';
// 
import {
	timeFormat
} from '@/uview/libs/function/timeFormat.js';
// ,
import {
	timeFrom
} from '@/uview/libs/function/timeFrom.js';
// ,colorGradient-,hexToRgb-rgb,rgbToHex-rgb
import {
	colorGradient,
	hexToRgb,
	rgbToHex
} from '@/uview/libs/function/colorGradient.js';
// guid
import {
	guid
} from '@/uview/libs/function/guid.js';
// ,info|success|warning|primary|default|error,uview.scss,js,
import {
	color
} from '@/uview/libs/function/color.js';
// type
import {
	type2icon
} from '@/uview/libs/function/type2icon.js';
// 
import {
	randomArray
} from "@/uview/libs/function/randomArray.js";

// 
import test from "@/uview/libs/function/test.js";
// 
import {
	random
} from "@/uview/libs/function/random.js";
// 
import {
	trim
} from "@/uview/libs/function/trim.js";
// toastuni.showToast
import {
	toast
} from "@/uview/libs/function/toast.js";

// 
import config from "@/uview/libs/config/config.js";
// fixedz-index
import zIndex from "@/uview/libs/config/zIndex.js";

const $u = {
	queryParams,
	route,
	timeFormat,
	date: timeFormat, // date
	timeFrom,
	colorGradient,
	guid,
	color,
	type2icon,
	randomArray,
	wranning,
	get: http.get,
	post: http.post,
	hexToRgb,
	rgbToHex,
	test,
	random,
	trim,
	type: ['primary', 'success', 'error', 'warning', 'info'],
	http,
	toast,
	config, // uView
	zIndex,
}

const install = (Vue) => {
	Vue.mixin(mixin);
	// Vue.mixin(vuexStore);
	// datetimeFormat
	Vue.filter('timeFormat', (timestamp, format) => {
		return timeFormat(timestamp, format);
	})
	Vue.filter('date', (timestamp, format) => {
		return timeFormat(timestamp, format);
	})
	// 
	Vue.filter('timeFrom', (timestamp, format) => {
		return timeFrom(timestamp, format);
	})
	Vue.prototype.$u = $u;
}

export default {
	install
}
