import  baseURL  from '../config'
import api from '../api'

let baseUrl = baseURL

/**
 * @param{string}url (baseUrl)
 * @param{Object}data 
 * @param{string}method (POST)psget
 * @param{string}contentType (JSONform)
 * @param{Object}headers  header 
 */
export default (url, data, method = 'POST', contentType = 'json', headers = {}) => {
	// url '/', 
	if (url.slice(0, 1) === '/') url = url.slice(1)
	// data
	for (let i in data) {
		if (typeof(data[i]) == 'undefined' || (data[i] != 0 && !data[i])) {
			delete data[i]
		}
	}
	// 
	if (!(/[A-Za-z]{4}/.test(contentType))) throw 'contentType jsonform'
	if (contentType.toUpperCase() === 'JSON') {
		headers['Content-Type'] = 'application/json; charset=UTF-8' // 
	} else {
		headers['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8' // 
	}
	
	// cid -- token
	let token = uni.getStorageSync('token')
	if (token) {
		headers.token = token
	}
	
	return new Promise(function(resolve, reject) {
		uni.request({
			url: baseUrl + url, 
			data: data,
			method: method,
			header: headers,
			success: function(res) {
				if (parseInt(res.statusCode) === 200) {
				
					if(res.data.code == 401){
						uni.showToast({
							icon:'none',
							title:''
						})
						setTimeout(()=>{
							uni.reLaunch({
								url:'../Login/Login'
							})
						})
					}else{
						resolve(res.data)
					}
				} else if (parseInt(res.statusCode) === 401) {
					// 401	
					throw Error(`${baseUrl}${url},${JSON.stringify(data)};401`);
				} else if (parseInt(res.statusCode) === 500) {
					// 	500				
					throw Error(`${baseUrl}${url},${JSON.stringify(data)};500`)
				} else {
					resolve(res.data)
				}
			},
			fail: function(err) {
				console.log(err)
				// uni.showToast({
				// 	icon: 'none',
				// 	title: err
				// })
			}
		})
	})
}
